<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input v-model="queryData.queryStr" class="filter-item" style="width: 300px;"
                      @keyup.native.enter="getListData"
                      placeholder="请输入用户手机号码、姓名"></el-input>

            <el-select v-model="queryData.type" placeholder="请选择">
                <el-option
                        v-for="item in operationTypes"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>

            <el-button class="filter-item" @click.stop="getListData" size="small">查询</el-button>
            <el-button type="primary" @click.stop="createRMBManualRecharge" v-if="permission.add" size="small">新增
            </el-button>
        </div>

        <el-table :data="tableData" border @current-change="handleTableCurrentChange">
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="120">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px"
                            v-if="scope.row.status===1&&scope.row.type!==3||scope.row.status===5">
                            <el-button
                                    type="text" size="small" @click.stop="showDialog(scope.row.id + '/examine','审核')"
                                    v-if="permission.verify">审核
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===1&&scope.row.type===3">
                            <el-button
                                    type="text" size="small" @click.stop="showDialog(scope.row.id + '/lock','冻结')"
                                    v-if="permission.verify">冻结
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===1">
                            <el-button
                                    type="text" size="small" @click.stop="showDialog(scope.row.id + '/delete','删除')"
                                    v-if="permission.delete">删除
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===3||scope.row.status===5">
                            <el-button
                                    type="text" size="small" @click.stop="showDialog(scope.row.id + '/unblocked','解冻')"
                                    v-if="permission.unblocked">
                                解冻
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryData.pageNum"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="queryData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageCount">
            </el-pagination>
        </div>
    </div>
</template>

<script>

    import fetch from '@/utils/fetch';

    import DCTP from '@/utils/dctp';

    import calc from "@/utils/calculate-big-num";
    
    const operationTypeScope = [{value: 0, label: '全部类型'},
        {value: 1, label: '冻结金额'},
        {value: 2, label: '划入金额'},
        {value: 3, label: '划出金额'}];

    const operationStatus = [{value: 1, label: '待审核'},
        {value: 2, label: '已审核'},
        {value: 3, label: '已冻结'},
        {value: 4, label: '已解冻'},
        {value: 5, label: '已冻结'}];

    export default {
        data() {
            return {
                queryData: {
                    queryStr: '',
                    type: 0,
                    category: '2',
                    pageNum: 1,   // 页数
                    pageSize: 10  // 每页数量
                },
                tableHeader: [{
                    prop: "userId",
                    label: "UID",
                    width: 90
                }, {
                    prop: "name",
                    label: "姓名",
                    width: 120
                }, {
                    prop: "phoneNum",
                    label: "手机号码",
                    width: 150
                }, {
                    prop: "nickName",
                    label: "用户名",
                    width: 150
                }, {
                    prop: "type",
                    label: "类型",
                    width: 150,
                    formatter(row, column, value) {
                        let item = DCTP.utils.findDataByValue(operationTypeScope, value, 'value');
                        return item ? item.label : '';
                    }
                }, {
                    prop: "status",
                    label: "状态",
                    width: 120,
                    formatter(row, column, value) {
                        let item = DCTP.utils.findDataByValue(operationStatus, value, 'value');
                        return item ? item.label : '';
                    }
                }, {
                    prop: "virtualCoinName",
                    label: "资产类型",
                    width: 120
                }, {
                    prop: "amount",
                    label: "金额",
                    width: 150,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    prop: "remark",
                    label: "备注信息",
                    width: 300
                }, {
                    prop: "createTime",
                    label: "创建时间",
                    width: 180,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value);
                    }
                }, {
                    prop: "auditTime",
                    label: "审核时间",
                    width: 180,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value);
                    }
                }, {
                    prop: "auditUserId",
                    label: "审核人",
                    width: 80,
                    formatter(row, column, value) {
                        return value === 0 ? '' : value;
                    }
                }],
                pageCount: 0,
                tableData: [],
                operationTypes: operationTypeScope,
                virtualCoinAndGoods: []
            }
        },
        methods: {
            handleSizeChange(val) {
                this.queryData.pageSize = val;
                this.getListData();
            },
            handleCurrentChange(val) {
                this.queryData.pageNum = val;
                this.getListData();
            },
            handleTableCurrentChange() {

            },
            getListData() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/manualoperation/query',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.pageCount = response.data.page.count;
                        }
                    }).catch();
                })
            },
            operation(url) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/manualoperation/' + url,
                        method: 'put'
                    }).then(function (response) {
                        _this.getMessage(response.msg, response.result);
                        if (response.result) {
                            _this.getListData();
                        }
                    }).catch();
                })
            },
            showDialog(url, text) {
                let _this = this;
                this.$confirm('确认' + text + '？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    _this.operation(url);
                }).catch(() => {
                    this.$message.info('操作已取消');
                });
            },
            createRMBManualRecharge() {
                this.$router.push({path: 'virtualManualRecharge'});
            },
            getVirtualCoinAndGoods() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/virtualcoinandgoods',
                        method: 'put',
                        data: {types: [1, 2]}
                    }).then(function (response) {
                        if (response.result) {
                            _this.virtualCoinAndGoods = response.data;
                        }
                    }).catch();
                })
            }
        },
        created() {
            this.getListData();
            this.getVirtualCoinAndGoods();
        }
    }

</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }
</style>



